<template>
  <!--  -->
  <div class="dean-management">
    <!--  -->
    <el-button type="primary" @click="fetchDeans"></el-button>
    <!--  -->
    <el-button type="success" @click="openAddDeanDialog"></el-button>
    <!--  -->
    <el-input
        v-model="search"
        size="default"
        placeholder=""
        style="margin: 20px 0;"
    />
    <!--  -->
    <el-table
        :data="deans.filter(dean => !search || dean.name.toLowerCase().includes(search.toLowerCase()))"
        style="width: 100%">
      <el-table-column prop="id" label="ID" width="180"></el-table-column>
      <el-table-column prop="name" label="" width="180"></el-table-column>
      <el-table-column prop="code" label="" width="180"></el-table-column>
      <el-table-column prop="remark" label="" width="180"></el-table-column>
      <el-table-column label="" width="180">
        <!--  -->
        <template v-slot="scope">
          <el-button @click="editDean(scope.row)" type="primary" size="small"></el-button>
          <el-button @click="deleteDean(scope.row.id)" type="danger" size="small"></el-button>
        </template>
      </el-table-column>
    </el-table>

    <!-- / -->
    <el-dialog v-model="dialogVisible" title="">
      <el-form :model="deanForm" :rules="rules" ref="deanFormRef">
        <el-form-item label="" prop="name">
          <el-input v-model="deanForm.name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="code">
          <el-input v-model="deanForm.code"></el-input>
        </el-form-item>
        <el-form-item v-if="!deanForm.id" label="" prop="password">
          <el-input type="password" v-model="deanForm.password"></el-input>
        </el-form-item>
        <el-form-item label="" prop="remark">
          <el-input v-model="deanForm.remark"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="saveDean"></el-button>
          <el-button @click="dialogVisible = false"></el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
import deanService from '@/services/deanService';

export default {
  name: 'DeanManagement',
  data() {
    return {
      // 
      deans: [],
      // 
      deanForm: {
        id: null,
        name: '',
        code: '',
        password: '123456789', // 
        remark: ''
      },
      // 
      dialogVisible: false,
      // 
      search: '',
      // 
      rules: {
        name: [{ required: true, message: '', trigger: 'blur' }],
        code: [{ required: true, message: '', trigger: 'blur' }],
        password: [{ required: true, message: '', trigger: 'blur' }],
        remark: [{ required: true, message: '', trigger: 'blur' }],
      }
    };
  },
  methods: {
    // 
    fetchDeans() {
      deanService.fetchDeans()
          .then(response => {
            this.deans = response.data;
          })
          .catch(error => {
            console.error(':', error);
          });
    },
    // 
    openAddDeanDialog() {
      this.deanForm = {
        id: null,
        name: '',
        code: '',
        password: '123456789', // 
        remark: ''
      };
      this.dialogVisible = true;
    },
    // 
    editDean(dean) {
      this.deanForm = { ...dean, password: '' }; // 
      this.dialogVisible = true;
    },
    // 
    saveDean() {
      this.$refs.deanFormRef.validate((valid) => {
        if (valid) {
          deanService.saveDean(this.deanForm)
              .then(() => {
                this.$message.success('');
                this.dialogVisible = false;
                this.fetchDeans(); // 
              })
              .catch(error => {
                console.error(':', error);
                this.$message.error('');
              });
        } else {
          this.$message.error('');
          return false;
        }
      });
    },
    // 
    deleteDean(id) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        deanService.deleteDean(id)
            .then(() => {
              this.$message.success('');
              this.fetchDeans(); // 
            })
            .catch(error => {
              console.error(':', error);
              this.$message.error('');
            });
      }).catch(() => {
        this.$message.info('');
      });
    }
  },
  mounted() {
    // 
    this.fetchDeans();
  }
};
</script>

<style scoped>
.dean-management {
  padding: 20px;
}
</style>
