<template>
  <!--  -->
  <div class="student-management">
    <!--  -->
    <el-button type="primary" @click="fetchStudents"></el-button>
    <!--  -->
    <el-button type="success" @click="openAddStudentDialog"></el-button>
    <!--  -->
    <el-input
        v-model="search"
        size="default"
        placeholder=""
        style="margin: 20px 0;"
    />
    <!--  -->
    <el-table
        :data="students.filter(student => !search || student.name.toLowerCase().includes(search.toLowerCase()))"
        style="width: 100%">
      <el-table-column prop="id" label="ID" width="180"></el-table-column>
      <el-table-column prop="name" label="" width="180"></el-table-column>
      <el-table-column prop="code" label="" width="180"></el-table-column>
      <el-table-column prop="className" label="" width="180"></el-table-column>
      <el-table-column prop="specialityName" label="" width="180"></el-table-column>
      <el-table-column prop="collegeName" label="" width="180"></el-table-column>
      <el-table-column label="" width="180">
        <!--  -->
        <template v-slot="scope">
          <el-button @click="editStudent(scope.row)" type="primary" size="small"></el-button>
          <el-button @click="deleteStudent(scope.row.id)" type="danger" size="small"></el-button>
        </template>
      </el-table-column>
    </el-table>

    <!-- / -->
    <el-dialog v-model="dialogVisible" title="">
      <el-form :model="studentForm" :rules="rules" ref="studentFormRef">
        <el-form-item label="" prop="name">
          <el-input v-model="studentForm.name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="code">
          <el-input v-model="studentForm.code"></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input type="password" v-model="studentForm.password"></el-input>
        </el-form-item>
        <el-form-item label="" prop="classId">
          <el-select v-model="studentForm.classId" placeholder="">
            <el-option
                v-for="classGroup in classGroups"
                :key="classGroup.id"
                :label="classGroup.name"
                :value="classGroup.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="saveStudent"></el-button>
          <el-button @click="dialogVisible = false"></el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
import studentService from '@/services/studentService';

export default {
  name: 'StudentManagement',
  data() {
    return {
      // 
      students: [],
      // 
      classGroups: [],
      // 
      studentForm: {
        id: null,
        name: '',
        code: '',
        password: '123456789', // 
        classId: ''
      },
      // 
      dialogVisible: false,
      // 
      search: '',
      // 
      rules: {
        name: [{required: true, message: '', trigger: 'blur'}],
        code: [{required: true, message: '', trigger: 'blur'}],
        password: [{required: true, message: '', trigger: 'blur'}],
        classId: [{required: true, message: '', trigger: 'change'}]
      }
    };
  },
  methods: {
    // 
    fetchStudents() {
      studentService.fetchStudents()
          .then(response => {
            this.students = response.data.map(item => ({
              id: item.student.id,
              name: item.student.name,
              code: item.student.code,
              password: item.student.password, // 
              classId: item.student.classId,
              className: item.classGroup.name,
              specialityName: item.speciality.name,
              collegeName: item.college.name
            }));
          })
          .catch(error => {
            console.error(':', error);
          });
    },
    // 
    fetchClassGroups() {
      studentService.fetchClassGroups()
          .then(response => {
            this.classGroups = response.data;
          })
          .catch(error => {
            console.error(':', error);
          });
    },
    // 
    openAddStudentDialog() {
      this.studentForm = {
        id: null,
        name: '',
        code: '',
        password: '123456789', // 
        classId: ''
      };
      this.dialogVisible = true;
    },
    // 
    editStudent(student) {
      this.studentForm = {...student, password: ''}; // 
      this.dialogVisible = true;
    },
    // 
    saveStudent() {
      this.$refs.studentFormRef.validate((valid) => {
        if (valid) {
          studentService.saveStudent(this.studentForm)
              .then(() => {
                this.$message.success('');
                this.dialogVisible = false;
                this.fetchStudents(); // 
              })
              .catch(error => {
                console.error(':', error);
                this.$message.error('');
              });
        } else {
          this.$message.error('');
          return false;
        }
      });
    },
    // 
    deleteStudent(id) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        studentService.deleteStudent(id)
            .then(() => {
              this.$message.success('');
              this.fetchStudents(); // 
            })
            .catch(error => {
              console.error(':', error);
              this.$message.error('');
            });
      }).catch(() => {
        this.$message.info('');
      });
    }
  },
  mounted() {
    // 
    this.fetchStudents();
    this.fetchClassGroups();
  }
};
</script>

<style scoped>
.student-management {
  padding: 20px;
}
</style>
