<template>
  <!--  -->
  <div class="home">
    <!--  -->
    <h1></h1>
    <p></p>
    <!--  -->
    <div class="features">
      <!--  -->
      <div class="feature">
        <h2></h2>
        <p v-if="students.length">{{ students.length }}</p>
        <p v-else>...</p>
        <el-carousel v-if="students.length" :interval="2000" type="card" height="200px" indicator-position="none">
          <el-carousel-item v-for="student in students" :key="student.student.id">
            <p>{{ student.student.name }} - {{ student.classGroup.name }} - {{ student.speciality.name }} - {{ student.college.name }}</p>
          </el-carousel-item>
        </el-carousel>
      </div>
      <!--  -->
      <div class="feature">
        <h2></h2>
        <p v-if="teachers.length">{{ teachers.length }}</p>
        <p v-else>...</p>
        <el-carousel v-if="teachers.length" :interval="2000" type="card" height="200px" indicator-position="none">
          <el-carousel-item v-for="teacher in teachers" :key="teacher.teacher.id">
            <p>{{ teacher.teacher.name }} - {{ teacher.college.name }}</p>
          </el-carousel-item>
        </el-carousel>
      </div>
      <!--  -->
      <div class="feature">
        <h2></h2>
        <p v-if="deans.length">{{ deans.length }}</p>
        <p v-else>...</p>
        <el-carousel v-if="deans.length" :interval="2000" type="card" height="200px" indicator-position="none">
          <el-carousel-item v-for="dean in deans" :key="dean.id">
            <p>{{ dean.name }} - {{ dean.remark }}</p>
          </el-carousel-item>
        </el-carousel>
      </div>
    </div>
  </div>
</template>

<script>
import studentService from '@/services/studentService';
import teacherService from '@/services/teacherService';
import deanService from '@/services/deanService';

export default {
  name: 'HomeView',
  data() {
    return {
      // 
      students: [],
      // 
      teachers: [],
      // 
      deans: [],
    };
  },
  created() {
    // 
    this.fetchStudents();
    this.fetchTeachers();
    this.fetchDeans();
  },
  methods: {
    // 
    async fetchStudents() {
      try {
        const response = await studentService.fetchStudents();
        this.students = response.data;
      } catch (error) {
        console.error(':', error);
      }
    },
    // 
    async fetchTeachers() {
      try {
        const response = await teacherService.fetchTeachers();
        this.teachers = response.data;
      } catch (error) {
        console.error(':', error);
      }
    },
    // 
    async fetchDeans() {
      try {
        const response = await deanService.fetchDeans();
        this.deans = response.data;
      } catch (error) {
        console.error(':', error);
      }
    },
  },
};
</script>

<style scoped>
.home {
  text-align: center;
  margin-top: 50px;
  font-family: 'Arial', sans-serif;
  color: #2c3e50;
}

h1 {
  font-size: 2.5em;
  margin-bottom: 10px;
}

p {
  font-size: 1.2em;
  margin-bottom: 30px;
}

.features {
  display: flex;
  justify-content: center;
  gap: 20px;
}

.feature {
  border: 1px solid #ddd;
  padding: 20px;
  border-radius: 10px;
  width: 300px;
  background-color: #f9f9f9;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.feature h2 {
  font-size: 1.5em;
  margin-bottom: 10px;
}

.feature p {
  font-size: 1em;
  color: #666;
}

.el-carousel__item h3 {
  color: #475669;
  font-size: 14px;
  opacity: 0.75;
  line-height: 200px;
  margin: 0;
}

.el-carousel__item:nth-child(2n) {
  background-color: #99a9bf;
}

.el-carousel__item:nth-child(2n+1) {
  background-color: #d3dce6;
}

.el-carousel__indicators {
  display: none;
}
</style>
