<template>
  <!--  -->
  <div class="class-management">
    <!--  -->
    <el-button type="primary" @click="openAddClassDialog"></el-button>
    <!--  -->
    <el-input
        v-model="search"
        size="default"
        placeholder=""
        style="margin: 20px 0;"
    />
    <!--  -->
    <el-table :data="filteredClasses" style="width: 100%">
      <el-table-column prop="classGroup.name" label="" width="180"></el-table-column>
      <el-table-column prop="classGroup.code" label="" width="180"></el-table-column>
      <el-table-column prop="speciality.name" label="" width="180"></el-table-column>
      <el-table-column prop="speciality.grade" label="" width="180"></el-table-column>
      <el-table-column prop="college.name" label="" width="180"></el-table-column>
      <el-table-column label="" width="300">
        <!--  -->
        <template v-slot="scope">
          <el-button @click="editClass(scope.row)" type="primary" size="small"></el-button>
          <el-button @click="deleteClass(scope.row.classGroup.id)" type="danger" size="small"></el-button>
          <el-button @click="assignCourse(scope.row.classGroup.id)" type="success" size="small"></el-button>
        </template>
      </el-table-column>
    </el-table>

    <!-- / -->
    <el-dialog v-model="dialogVisible" title="">
      <el-form :model="classForm" :rules="rules" ref="classFormRef">
        <el-form-item label="" prop="name">
          <el-input v-model="classForm.name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="code">
          <el-input v-model="classForm.code"></el-input>
        </el-form-item>
        <el-form-item label="" prop="remark">
          <el-input v-model="classForm.remark"></el-input>
        </el-form-item>
        <el-form-item label="" prop="specialityId">
          <el-select v-model="classForm.specialityId" placeholder="">
            <el-option
                v-for="speciality in specialities"
                :key="speciality.id"
                :label="speciality.name"
                :value="speciality.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="saveClass"></el-button>
          <el-button @click="dialogVisible = false"></el-button>
        </el-form-item>
      </el-form>
    </el-dialog>

    <!--  -->
    <el-dialog v-model="assignCourseDialogVisible" title="">
      <el-table :data="assignedCourses" style="width: 100%">
        <el-table-column prop="course.name" label="" width="180"></el-table-column>
        <el-table-column prop="term.name" label="" width="180"></el-table-column>
        <el-table-column prop="course.remark" label="" width="180"></el-table-column>
        <el-table-column label="" width="180">
          <!--  -->
          <template v-slot="scope">
            <el-button @click="removeCourse(scope.row.id)" type="danger" size="small"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-form>
        <el-form-item label="">
          <el-select v-model="selectedCourseId" placeholder="">
            <el-option
                v-for="course in availableCourses"
                :key="course.id"
                :label="course.name"
                :value="course.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="addCourse"></el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
import classapiService from '@/services/classapiService';

export default {
  name: 'ClassManagement',
  data() {
    return {
      // 
      classes: [],
      // 
      specialities: [],
      // 
      classForm: {
        id: null,
        name: '',
        code: '',
        specialityId: null,
        remark: ''
      },
      // 
      dialogVisible: false,
      assignCourseDialogVisible: false,
      // ID
      selectedClassId: null,
      // 
      assignedCourses: [],
      // 
      availableCourses: [],
      // ID
      selectedCourseId: null,
      // 
      search: '',
      // 
      rules: {
        name: [{ required: true, message: '', trigger: 'blur' }],
        code: [{ required: true, message: '', trigger: 'blur' }],
        specialityId: [{ required: true, message: '', trigger: 'change' }],
        remark: [{ required: true, message: '', trigger: 'blur' }]
      }
    };
  },
  computed: {
    // 
    filteredClasses() {
      return this.classes.filter(cls => !this.search || cls.classGroup.name.toLowerCase().includes(this.search.toLowerCase()));
    }
  },
  methods: {
    // 
    fetchClasses() {
      classapiService.fetchClasses()
          .then(response => {
            this.classes = response.data;
          })
          .catch(error => {
            console.error(':', error);
          });
    },
    // 
    fetchSpecialities() {
      classapiService.fetchSpecialities()
          .then(response => {
            this.specialities = response.data;
          })
          .catch(error => {
            console.error(':', error);
          });
    },
    // 
    openAddClassDialog() {
      this.classForm = {
        id: null,
        name: '',
        code: '',
        specialityId: null,
        remark: ''
      };
      this.dialogVisible = true;
    },
    // 
    editClass(classData) {
      this.classForm = {
        id: classData.classGroup.id,
        name: classData.classGroup.name,
        code: classData.classGroup.code,
        specialityId: classData.speciality.id,
        remark: classData.classGroup.remark
      };
      this.dialogVisible = true;
    },
    // 
    saveClass() {
      this.$refs.classFormRef.validate((valid) => {
        if (valid) {
          const classData = {
            id: this.classForm.id,
            name: this.classForm.name,
            code: this.classForm.code,
            specialityId: this.classForm.specialityId,
            remark: this.classForm.remark
          };
          classapiService.saveClass(classData)
              .then(() => {
                this.$message.success('');
                this.dialogVisible = false;
                this.fetchClasses(); // 
              })
              .catch(error => {
                console.error(':', error);
                this.$message.error('');
              });
        } else {
          this.$message.error('');
          return false;
        }
      });
    },
    // 
    deleteClass(id) {
      classapiService.deleteClass(id)
          .then(() => {
            this.$message.success('');
            this.fetchClasses(); // 
          })
          .catch(error => {
            if (error.response && error.response.status === 500) {
              this.$message.error('');
            } else {
              console.error(':', error);
              this.$message.error('');
            }
          });
    },
    // 
    assignCourse(classId) {
      this.selectedClassId = classId;
      this.assignCourseDialogVisible = true;
      this.fetchAssignedCourses(classId);
      this.fetchAvailableCourses();
    },
    // 
    fetchAssignedCourses(classId) {
      classapiService.fetchAssignedCourses(classId)
          .then(courses => {
            this.assignedCourses = courses;
          })
          .catch(error => {
            console.error(':', error);
          });
    },
    // 
    fetchAvailableCourses() {
      classapiService.fetchAvailableCourses()
          .then(response => {
            this.availableCourses = response.data;
          })
          .catch(error => {
            console.error(':', error);
          });
    },
    // 
    addCourse() {
      // 
      if (this.assignedCourses.some(course => course.course.id === this.selectedCourseId)) {
        this.$message.error('');
        return;
      }

      const courseAssignment = {
        classId: this.selectedClassId,
        courseId: this.selectedCourseId
      };
      classapiService.addCourse(courseAssignment)
          .then(() => {
            this.$message.success('');
            this.fetchAssignedCourses(this.selectedClassId);
          })
          .catch(error => {
            console.error(':', error);
            this.$message.error('');
          });
    },
    // 
    removeCourse(assignmentId) {
      classapiService.removeCourse(assignmentId)
          .then(() => {
            this.$message.success('');
            this.fetchAssignedCourses(this.selectedClassId);
          })
          .catch(error => {
            console.error(':', error);
            this.$message.error('');
          });
    }
  },
  mounted() {
    // 
    this.fetchClasses();
    this.fetchSpecialities();
  }
};
</script>

<style scoped>
.class-management {
  padding: 20px;
}
</style>
