<template>
  <!--  -->
  <div class="student-info">
    <h2></h2>
    <!--  -->
    <el-card v-if="studentInfo">
      <p><strong>:</strong> {{ studentInfo.student.name }}</p>
      <p><strong>:</strong> {{ studentInfo.student.code }}</p>
      <p><strong>:</strong> {{ studentInfo.classGroup.name }}</p>
      <p><strong>:</strong> {{ studentInfo.speciality.name }}</p>
      <p><strong>:</strong> {{ studentInfo.college.name }}</p>
    </el-card>
  </div>
</template>

<script>
import studentService from '@/services/studentService';

export default {
  name: 'StudentInfo',
  data() {
    return {
      // 
      studentInfo: null,
    };
  },
  methods: {
    // 
    fetchStudentInfo() {
      const userCode = localStorage.getItem('user-code');
      if (userCode) {
        studentService.fetchStudentInfo(userCode)
            .then(response => {
              this.studentInfo = response.data;
            })
            .catch(error => {
              console.error(':', error);
            });
      }
    },
  },
  mounted() {
    // 
    this.fetchStudentInfo();
  },
};
</script>

<style scoped>
.student-info {
  padding: 20px;
}

.el-card {
  padding: 20px;
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

p {
  font-size: 16px;
  color: #333;
  margin: 10px 0;
}

strong {
  color: #2c3e50;
}
</style>
