<template>
  <!--  -->
  <div class="teacher-management">
    <!--  -->
    <el-button type="primary" @click="fetchTeachers"></el-button>
    <!--  -->
    <el-button type="success" @click="openAddTeacherDialog"></el-button>
    <!--  -->
    <el-input
        v-model="search"
        size="default"
        placeholder=""
        style="margin: 20px 0;"
    />
    <!--  -->
    <el-table
        :data="teachers.filter(teacher => !search || teacher.name.toLowerCase().includes(search.toLowerCase()))"
        style="width: 100%">
      <el-table-column prop="id" label="ID" width="180"></el-table-column>
      <el-table-column prop="name" label="" width="180"></el-table-column>
      <el-table-column prop="code" label="" width="180"></el-table-column>
      <el-table-column prop="collegeName" label="" width="180"></el-table-column>
      <el-table-column label="" width="240">
        <!--  -->
        <template v-slot="scope">
          <el-button @click="editTeacher(scope.row)" type="primary" size="small"></el-button>
          <el-button @click="deleteTeacher(scope.row.id)" type="danger" size="small"></el-button>
          <el-button @click="assignCourse(scope.row)" type="success" size="small"></el-button>
        </template>
      </el-table-column>
    </el-table>

    <!-- / -->
    <el-dialog v-model="dialogVisible" title="">
      <el-form :model="teacherForm" :rules="rules" ref="teacherFormRef">
        <el-form-item label="" prop="name">
          <el-input v-model="teacherForm.name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="code">
          <el-input v-model="teacherForm.code"></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input type="password" v-model="teacherForm.password"></el-input>
        </el-form-item>
        <el-form-item label="" prop="collegeId">
          <el-select v-model="teacherForm.collegeId" placeholder="">
            <el-option
                v-for="college in colleges"
                :key="college.id"
                :label="college.name"
                :value="college.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="saveTeacher"></el-button>
          <el-button @click="dialogVisible = false"></el-button>
        </el-form-item>
      </el-form>
    </el-dialog>

    <!--  -->
    <el-dialog v-model="assignCourseDialogVisible" title="">
      <el-table :data="assignedCourses" style="width: 100%">
        <el-table-column prop="name" label="" width="180"></el-table-column>
        <el-table-column prop="term.name" label="" width="180"></el-table-column>
        <el-table-column prop="remark" label="" width="180"></el-table-column>
        <el-table-column label="" width="180">
          <!--  -->
          <template v-slot="scope">
            <el-button @click="removeCourse(scope.row)" type="danger" size="small"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-form>
        <el-form-item label="">
          <el-select v-model="selectedCourseId" placeholder="">
            <el-option
                v-for="course in availableCourses"
                :key="course.id"
                :label="course.name"
                :value="course.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="addCourse"></el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
import teacherService from '@/services/teacherService';

export default {
  name: 'TeacherManagement',
  data() {
    return {
      // 
      teachers: [],
      // 
      colleges: [],
      // 
      teacherForm: {
        id: null,
        name: '',
        code: '',
        password: '123456789', // 
        collegeId: ''
      },
      // 
      dialogVisible: false,
      assignCourseDialogVisible: false,
      // 
      selectedTeacher: null,
      // 
      assignedCourses: [],
      availableCourses: [],
      selectedCourseId: null,
      // 
      search: '',
      // 
      rules: {
        name: [{required: true, message: '', trigger: 'blur'}],
        code: [{required: true, message: '', trigger: 'blur'}],
        password: [{required: true, message: '', trigger: 'blur'}],
        collegeId: [{required: true, message: '', trigger: 'change'}]
      }
    };
  },
  methods: {
    // 
    fetchTeachers() {
      teacherService.fetchTeachers()
          .then(response => {
            this.teachers = response.data.map(item => ({
              id: item.teacher.id,
              name: item.teacher.name,
              code: item.teacher.code,
              password: item.teacher.password,
              collegeId: item.teacher.collegeId,
              collegeName: item.college.name
            }));
          })
          .catch(error => {
            console.error(':', error);
          });
    },
    // 
    fetchColleges() {
      teacherService.fetchColleges()
          .then(response => {
            this.colleges = response.data;
          })
          .catch(error => {
            console.error(':', error);
          });
    },
    // 
    openAddTeacherDialog() {
      this.teacherForm = {
        id: null,
        name: '',
        code: '',
        password: '123456789', // 
        collegeId: ''
      };
      this.dialogVisible = true;
    },
    // 
    editTeacher(teacher) {
      this.teacherForm = {...teacher, password: ''}; // 
      this.dialogVisible = true;
    },
    // 
    saveTeacher() {
      this.$refs.teacherFormRef.validate((valid) => {
        if (valid) {
          teacherService.saveTeacher(this.teacherForm)
              .then(() => {
                this.$message.success('');
                this.dialogVisible = false;
                this.fetchTeachers(); // 
              })
              .catch(error => {
                console.error(':', error);
                this.$message.error('');
              });
        } else {
          this.$message.error('');
          return false;
        }
      });
    },
    // 
    deleteTeacher(id) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        teacherService.deleteTeacher(id)
            .then(() => {
              this.$message.success('');
              this.fetchTeachers(); // 
            })
            .catch(error => {
              console.error(':', error);
              this.$message.error('');
            });
      }).catch(() => {
        this.$message.info('');
      });
    },
    // 
    assignCourse(teacher) {
      this.selectedTeacher = teacher;
      this.assignCourseDialogVisible = true;
      this.fetchAssignedCourses(teacher.id);
      this.fetchAvailableCourses();
    },
    // 
    fetchAssignedCourses(teacherId) {
      teacherService.fetchAssignedCourses(teacherId)
          .then(courses => {
            this.assignedCourses = courses;
          })
          .catch(error => {
            console.error(':', error);
          });
    },
    // 
    fetchAvailableCourses() {
      teacherService.fetchAvailableCourses()
          .then(response => {
            this.availableCourses = response.data;
          })
          .catch(error => {
            console.error(':', error);
          });
    },
    // 
    addCourse() {
      if (this.assignedCourses.some(course => course.id === this.selectedCourseId)) {
        this.$message.error('');
        return;
      }
      const courseAssignment = {
        teacherId: this.selectedTeacher.id,
        courseId: this.selectedCourseId
      };
      teacherService.addCourse(courseAssignment)
          .then(() => {
            this.$message.success('');
            this.fetchAssignedCourses(this.selectedTeacher.id);
          })
          .catch(error => {
            console.error(':', error);
            this.$message.error('');
          });
    },
    // 
    removeCourse(course) {
      teacherService.removeCourse(this.selectedTeacher.id, course.id)
          .then(() => {
            this.$message.success('');
            this.fetchAssignedCourses(this.selectedTeacher.id);
          })
          .catch(error => {
            console.error(':', error);
            this.$message.error('');
          });
    }
  },
  mounted() {
    // 
    this.fetchTeachers();
    this.fetchColleges();
  }
};
</script>

<style scoped>
.teacher-management {
  padding: 20px;
}
</style>
